
import { defineComponent } from 'vue'

import { useRouter } from 'vue-router'

export default defineComponent({

  name: 'Navi',

  props: {
      title: {
        type: String,
        default: ''
      },
      rightText: {
        type: String,
        default: ''
      },
      fixed: {
        type: Boolean,
        default: false
      }
    },
  
  setup (props, {emit}) {
    let router: any = useRouter();

    let back: () => void = (): void => {
        router.go(-1);
      }


      let clickRight: () => void = (): void => {
        emit('click-right');
      }

    return {
      back,
      clickRight
    }
  }
})
