
import { defineComponent, ref, onMounted } from 'vue'

import Navi from "../components/Navi.vue"
import Bg from "../components/Bg.vue"
import OrderPro from "../components/OrderPro.vue"

import axios from 'axios'

import {Toast, Dialog} from 'vant'

import { useRouter } from "vue-router"

import {tool} from '../tool/tool'

export default defineComponent({
  name: "Order",
  components: {
    Navi,
    Bg,
    OrderPro
  },
  setup () {

    let router: any = useRouter();

    //激活tab的下标, 0 ==> 全部(包含进行中 + 已完成), 1 ==> 进行中, 2 ==> 已完成
    let active: any = ref<number>(0);

    let count: any = ref<number>(0);

    let total: any = ref<number>(0);

    //标签页的数据
    let tabsData: any = ref<Array<any>>([
        {
          title: '全部'
        },
        {
          title: '进行中'
        },
        {
          title: '已完成'
        }
      ]);

    let orderData: any = ref<Array<any>>([]);

    //获取我的订单
    let getOrderData: () => void = (): void => {

      
      axios({
          method: 'get',
          url: '/findOrder',
          params: {
            status: active.value
          }
        }).then((result: any): void => {
          
          
          if (result.data.code === 700) {
            Toast(result.data.msg);
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 70000) {
            //获取订单编号
            let orderNo: Array<string> = [];
            result.data.result.forEach((item: any): void => {
              item.small_img = item.smallImg;
              if (orderNo.indexOf(item.oid) === - 1) {
                orderNo.push(item.oid);
              }
            })

            
            let orderDatas: any = [];
            //按照订单编号分类
            orderNo.forEach((v: any): void => {

              let currentOrder: any = {
                oid: v,
                product: [],
                count: 0,
                total: 0
              };

              result.data.result.forEach((item: any): void => {
                if (v === item.oid) {

                  if (currentOrder.status === undefined) {
                    currentOrder.status = item.status;
                    currentOrder.date = tool.formatDate(item.createdAt, 'yyyy-MM-dd hh:mm:ss');
                  }

                  currentOrder.product.push(item);
                  currentOrder.count += item.count;
                  currentOrder.total += item.count * item.price;
                
                }
              })

              orderDatas.push(currentOrder);

            })

            

            orderData.value = orderDatas;

          }


        }).catch((err: any): void => {
          
          
        })
    }

    //切换订单状态
    let toggleTab: () => void = () => {
      
      getOrderData();
    }

    //确认收货
    let confirm: (item: any, index: number) => void = (item: any, index: number): void => {
      Dialog.confirm({
        title: "收货提示",
        message: '是否确定收货！',
        confirmButtonColor: "#0C34BA",
      }).then((): void => {
        
        axios({
          method: 'post',
          url: '/receive',
          data: {
            oid: item.oid
          }
        }).then((result: any): void => {
          

          Toast(result.data.msg);

          if (result.data.code === 700) {
            
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 80000) {
            if (active.value === 0) {
              item.status = 2;
              
            } else if (item.status === 1) {
              //删除页面的订单数据
              orderData.value.splice(index, 1);
            }
          }


        }).catch((err: any): void => {
          
          
        })
      }).catch((err: any): void => {
        
        
      })
    }

    //删除订单
    let removeOrder: (oid: string, index: number) => void = (oid: string, index: number): void => {
      Dialog.confirm({
        title: "删除订单",
        message: '是否确定删除该订单！',
        confirmButtonColor: "#0C34BA",
      }).then((): void => {
        
        axios({
          method: 'post',
          url: '/removeOrder',
          data: {
            oid
          }
        }).then((result: any): void => {
          

           Toast(result.data.msg);

          if (result.data.code === 700) {
           
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 90000) {
            orderData.value.splice(index, 1);
          }

        }).catch((err: any): void => {
          
          
        })
      }).catch((err: any): void => {
        
      })
    }


    onMounted(() => {
      getOrderData();
    })

    return {
      active,
      count,
      total,
      tabsData,
      orderData,
      toggleTab,
      confirm,
      removeOrder
    };
  }
})
